.ImgSection {
    max-width: 1200px;
    margin: 25px auto;
}

.Img {
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.homeTitle {
    color: #000;
    margin: 75px auto -75px;
    max-width: 1200px;
    text-align: center;
}

