.menuBtn {
    display: block;
    width: 50px;
    height: 50px;
    position: relative;
    z-index: 10;
    background-color: #fab131;
    border-radius: 50%;
    box-shadow: 0 3px 0 rgba(0, 0, 0, .1);
    border-width: 0;
}

.menuBtn span,
.menuBtn span::before,
.menuBtn span::after {
    position: absolute;
    top: 50%;
    margin-top: -1px;
    left: 50%;
    margin-left: -10px;
    width: 20px;
    height: 2px;
    background-color: #fff;
}

.menuBtn span::before,
.menuBtn span::after {
    content: '';
    display: block;
    transition: 0.4s;
}

.menuBtn span::before {
    transform: translateY(-5px);
}

.menuBtn span::after{
    transform: translateY(5px);
}

.menuBtnActive {
    position: fixed;
    top: 10px;
    right: 20px;
    background-color: #fff;
}

.menuBtnActive span {
    height: 0;
}

.menuBtnActive span:before {
    transform: rotate(45deg);
    background-color: gray;

}

.menuBtnActive span:after{
    transform: rotate(-45deg);
    background-color: gray;

}